@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption&family=Poppins:wght@500&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 8vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    *-webkit-animation: App-logo-spin infinite 20s linear;
    *        animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: url(/Nearby_star_tracking/static/media/334517.bdeabd94.jpg);
  /*background-color: #282c34;*/
  min-height: 8vh;
  display: -webkit-flex;
  display: flex;
  /*flex-direction: column;*/
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: left;
          justify-content: left;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App-logo {
    height: 28px;
    margin-left: 48px;
    margin-bottom: 10px;
}

.App-header {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 80px;
    width: 100vw;
    color: rgb(226, 241, 11);
    /*background-color: #00305C;*/
    box-shadow: rgb(220, 220, 220) 0px 2px 10px;
}

.title {
    margin-left: 10px;
    font-size: 30px;
    height: 18px;
    font-weight: bold;
}
.footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin-bottom: 15px;
    width: 100%;
    text-align: center;
    color: #0430f1;
    font-size: 12px;
}
.main {
    position: relative;
    height: 760px;
}

.left-side {
    position: absolute;
    top: 0;
    left: 0;
    height: 760px;
    width: 420px;
    padding: 20px;
}

.right-side {
    margin-left: 420px;
    height: 760px;
}

.sat-setting {
    position: center;
    width: 100%;
    text-align: center;
    -webkit-align-content: center;
            align-content: center;
    padding: 10px 15px 0 15px;
    background: #83f1df;
    border: 1px solid #000000;
    border-radius: 6px;
}

.setting-fields {
    margin: 10px;
    margin-right: 15px;
}

.show-nearby {
    display: -webkit-flex;
    display: flex;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top:2px;
}

.show-nearby .ant-form-item-control {
    text-align: center;
}

.sat-setting .ant-form-item {
    margin-bottom: 12px;
}

.sat-list-box {
    padding: 10px;
    border: 1px solid rgb(0, 0, 0);
    border-radius: 6px;
    margin-top: 10px;
    height: 410px;
    background-color: #83f1df;
}

.sat-list {
    height: 300px;
    overflow-y: scroll;
}




.sat-list-btn {
    text-transform: none;
    padding: 10px 36px;
    background: linear-gradient(#282c34, #1b528d);
    font-size: 1em;
    color: #66faf3;
    margin-bottom: 10px;
}

.spin-box {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 370px;
}
.map-box {
    padding: 10px;
    border: 1px solid rgb(255, 148, 148);
    border-radius: 6px;
    margin-top: 20px;
    height: 750px;
    width: 1400px;
    background-color: rgba(233, 236, 192, 0.932);
}

.map,
.track,
.spinner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

.map {
    z-index: 0;
}

.track {
    z-index: 100;
}

.spinner {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 600px;
    width: 960px;
}

.hint {
    color: #993131;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    width: 100%;
}
body {
  margin: 0;
  font-family: 'PT Sans Caption', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(193, 225, 241);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

